import React, { Component } from 'react';

class Portfolio extends Component {
  render() {
    return (
      <section id="portfolio">
      <div className="row">
         <div className="twelve columns collapsed">
            <h1>Algunos de mis cursos realizados el ultimo tiempo, como dije mi idea es siempre aprender y no solo desarrollo sino habilidades personales que ayuden a mejorar y potenciar equipos</h1>
            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              <div key='agile' className="columns portfolio-item">
                  <div className="item-wrap">                    
                          <img alt='agile' src='https://iasanchez.github.io/cv-pages/images/agile.PNG' />
                  <div className="link-icon"><i className="fa fa-link"></i></div>
                  </div>
                </div>
            </div>
          </div>
      </div>
   </section>
    );
  }
}

export default Portfolio;
